import React, { useRef } from 'react'
import lang from '../utils/languageConstants';
import { useDispatch, useSelector } from 'react-redux';
import openai from '../utils/openai';
import { API_OPTIONS } from '../utils/constants';
import { addGptMovieResult } from '../utils/gptSlice';

const GptSearchBar = () => {
  const dispatch = useDispatch();
  const langKey = useSelector(store => store.config.lang);
  const searchText = useRef(null);

  //Search Movie in tmdb
  const searchMovieTmdb = async(movie)=> {
    const data =await  fetch("https://api.themoviedb.org/3/search/movie?query="+ movie +"&include_adult=false&language=en-US&page=1", API_OPTIONS);
    const json = await data.json();
    return json.results;
  }
  const handleGptSearchClick = async ()=> {
    const gptQuery = "Act as a Movie Recommendation System and Suggest some movie name for the query " + searchText.current.value + ". only give me names of 5 movies, comma seperated like the example result given ahead. Example Result: Pathan, Sholay, Don, Players, Koi Mil Gaya";
    //make api call to GPT API and get movie results
    const gptResults =  await openai.chat.completions.create({
      messages: [{ role: 'user', content: gptQuery }],
      model: 'gpt-3.5-turbo',
    });
  

  const gptMovies = gptResults.choices?.[0]?.message?.content.split(",")

//For each movie search  the movie on tmdb
const promiseArray = gptMovies.map(movie=> searchMovieTmdb(movie));
//we will get 5 promises in an array.

    //convert array of promises to data after promises are resolved
    const tmdbResults = await Promise.all(promiseArray);


    dispatch(addGptMovieResult({movieNames:gptMovies, movieResults: tmdbResults}));

}
  return (

    <div className="pt-[35%] md:pt-[10%] flex justify-center ">
    <form
      className="w-full md:w-1/2 bg-black grid grid-cols-12"
      onSubmit={(e) => e.preventDefault()}
    >
      <input
        
        type="text"
        ref={searchText}
        className=" p-4 m-4 col-span-9 rounded-lg"
        placeholder={lang[langKey].gptSearchPlaceholder}
      />
      <button
        className="col-span-3 m-4 py-2 px-4 bg-red-700 text-white rounded-lg " 
        onClick={handleGptSearchClick}>
        {lang[langKey].search}
      
      </button>
    </form>
  </div>
);
};
  


export default GptSearchBar